import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar,IonButton,IonIcon,IonGrid,IonRow,IonCol,IonDatetime
  ,IonItem,IonLabel,IonItemDivider, IonAvatar,IonModal } from '@ionic/react';
import { menu,stopwatch,add,removeCircle, removeCircleOutline, addCircleOutline, stopwatchOutline

  } from 'ionicons/icons';
import './Tab3.css';
import {format} from "date-fns";
import React, { useState } from 'react';
import {  useMutation,useQueryClient} from "react-query";
import { submitMeal } from "../api";
import { useHistory } from "react-router-dom"

const Tab3: React.FC = () => {
  const [selectedDate, setSelectedDate] = useState<string>(format(new Date(),"yyyy-MM-dd HH:mm"));
  const [selectedItem, setSelectedItem] = useState<string>('');
  const [value, setValue] = useState(5);
  const [showModal, setShowModal] = useState(false);
  const queryClient = useQueryClient();
  const history = useHistory()

  const mutation = useMutation(() =>
    submitMeal(selectedItem,value,selectedDate),
    {
      onSuccess: () => {
        queryClient.invalidateQueries();
      },
    }
      )

  const onclickSubmit = async () => {
    mutation.mutate()
    history.push('/tab2')
  };


  return (
    <IonPage>
      <IonHeader className="ion-no-border" mode="ios">
      <IonToolbar className="ion-padding-start ion-padding-end ion-padding-top ion-padding-bottom">
  <IonIcon size="large" icon={menu}  slot="start" className="" />
    <IonTitle><h3>Rice App</h3></IonTitle>
  </IonToolbar>
      </IonHeader>
      <IonContent>

      <IonTitle color="dark" className="ion-padding-bottom">Pick meal</IonTitle>
      <IonGrid class="ion-justify-content-center">
         <IonRow>
         <IonCol size="4">

         <IonAvatar className="ion-margin" color="primary" onClick={() => {
        setShowModal(true)
setSelectedItem('Rice')

}}>
    <img src={process.env.PUBLIC_URL+
                        `/assets/images/Rice.jpg`}
                   alt="" />

      </IonAvatar>
      <p>Harry styles</p>
           </IonCol>

           <IonCol size="4">

           <IonAvatar className="ion-margin" color="primary" onClick={() => {
        setShowModal(true)
setSelectedItem('Corn')

}}>
<img src={process.env.PUBLIC_URL+
                        `/assets/images/Corn.jpg`}
                   alt="" />

  </IonAvatar>
  <p>Harry styles</p>

       </IonCol>
       <IonCol size="4">

<IonAvatar className="ion-margin"  onClick={() => {
   setShowModal(true)
setSelectedItem('avocado')

}}>
<img src={process.env.PUBLIC_URL+
                        `/assets/images/avocado.jpg`}
                   alt="" />

  </IonAvatar>
  <p>Harry styles</p>

       </IonCol>

        </IonRow>
        </IonGrid>

        <IonModal isOpen={showModal} cssClass="ion-text-center">
        <IonPage>
        <IonContent className="ion-text-center">
<img className="img-w" src={process.env.PUBLIC_URL+
                        `/assets/images/${selectedItem}.jpg`}
                   alt="" />
         <p className="ion-text-center">Your selected item is {selectedItem}</p>
        {/* Pick Time     {selectedDate} */}

        <div className="ion-text-center">
        <IonIcon size="large" icon={addCircleOutline} onClick={()=> setValue(value+1 as number)}/>
          <h5 className="px-5">{value} Oz</h5>


              <IonIcon size="large" icon={removeCircleOutline} onClick={()=> {
              if(value <= 1) {
                setValue(1)
              }else {
                setValue(value-1 as number)
              }}
            }
              />

       </div>


    <IonItem class="ion-padding-start">
    <IonIcon size="large" icon={stopwatchOutline}/>
          <IonItem >
          <IonDatetime displayFormat="DDD, DD-MMM-YY HH:mm"
          pickerFormat="DD-MMM-YY HH:mm"
          value={selectedDate}
          onIonChange={e => setSelectedDate(format(new Date( e.detail.value!), "yyyy-MM-dd HH:mm"))}>

         </IonDatetime>
        </IonItem>

        </IonItem>
        <IonButton expand="block" color="secondary" className="ion-margin"onClick={() => {
onclickSubmit();
setShowModal(false)

         } }>Confirm</IonButton>
        <IonButton expand="block" color="secondary" className="ion-margin" onClick={() => setShowModal(false)}>Cancel</IonButton>
        </IonContent>
        </IonPage>
      </IonModal>

      </IonContent>

    </IonPage>
  );
};

export default Tab3;
