import axios from "axios";

export const getMeals = async (startDate:any, endDate:any) => {

    try {
        const response = await axios.get(`http://ricy-env.eba-mgkejafd.ap-southeast-1.elasticbeanstalk.com/api/filter?startDate='${startDate}'&endDate=${endDate}`)

        return response.data.meals
    }
    catch (err) {
        throw new Error(err);
      }

}


export const submitMeal = async (meal_name:any, oz:any, eat_at:any ) => {
    try {
      const response = await axios.post("http://ricy-env.eba-mgkejafd.ap-southeast-1.elasticbeanstalk.com/api/meals", {
        name: meal_name,
        oz: oz,
        eat_at,
      });

      return response.data.data.restaurant;
    } catch (err) {
        throw new Error(err);
    }
  };



export const deleteMeals = async (id:any) => {

    try {
        const response = await axios.delete(`http://ricy-env.eba-mgkejafd.ap-southeast-1.elasticbeanstalk.com/api/meals/${id}`)

        return response.data.meals
    }
    catch (err) {
        throw new Error(err);
      }

}